	module.exports = {"data":{"markdownRemark":{"html":"<h2>Orthogonality</h2>\n<p>In mathematics and linear algebra, two vectors u and v are said to be orthogonal when their dot product is 0:<br>\n<img src=\"http://mathworld.wolfram.com/images/equations/OrthogonalVectors/Inline3.gif\" alt=\"dot product\"><br>\nOrthogonality can be thought of as <a href = \"http://mathworld.wolfram.com/Perpendicular.html\">perpendicularity</a> generalized to higher dimensional vector spaces, since the two are the same- they imply that a right angle is formed by the line, plane, or vector. </p>\n<p><img src=\"http://mathworld.wolfram.com/images/eps-gif/Perpendicular_1000.gif\" alt=\"right angle\"></p>\n<p>The following are all examples of orthogonality: </p>\n<ol>\n<li>If two vectors are perpendicular, that is, they meet or intersect at a right (90 degree) angle, they are orthogonal. </li>\n<li>Two vectors are orthogonal if their inner product (dot product) is equal to 0. </li>\n<li>Two vector subspaces A and B in V are orthogonal if every vector in V is orthogonal to every vector in B. </li>\n</ol>\n<!-- <a href='https://github.com/freecodecamp/guides/blob/master/README.md' target='_blank' rel='nofollow'>This quick style guide will help ensure your pull request gets accepted</a>.\n\n <!--  The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<ol>\n<li><a href=\"http://mathworld.wolfram.com/Orthogonal.html\">http://mathworld.wolfram.com/Orthogonal.html</a></li>\n<li>\n<p><a href=\"http://mathworld.wolfram.com/Perpendicular.html\">http://mathworld.wolfram.com/Perpendicular.html</a></p>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n</li>\n</ol>","fields":{"slug":"/mathematics/linear-algebra/orthogonality/"},"frontmatter":{"title":"Orthogonality"}}},"pathContext":{"slug":"/mathematics/linear-algebra/orthogonality/"}}

